import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ServiceService } from 'src/app/service.service';

interface News{
  articles:any,

}

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  newsData:any;
  constructor(public rest: ServiceService,private router: Router) { }

  async ngOnInit() {
    var data:any = await this.rest.getNewsData();

    console.log(data.articles);
    this.newsData = data.articles;
  }

  async SelectedNewsCategory(event:any){
    console.log(event.target.value);
    let category:string = event.target.value
    var data:any = await this.rest.getCategoryNewsData(category.toLowerCase());

    this.newsData = data.articles;
  }

  readMore(element:any){
    this.rest.setNewsElement(element);

    window.open(element.url);
  }

}
